.accordeon {
	&__head {
		position: relative;
		cursor: pointer;
		font-size: 30px;
		font-weight: 900;
		color: $white;
		line-height: 1;
		padding: 30px 65px 28px 43px;
		background-color: $orange;
		@include sm {
			font-size: 20px;
			padding: 15px 30px 15px 15px; }
		&:before,
		&:after {
			content: "";
			position: absolute;
			transition: transform .4s;
			background-color: $white; }
		&:before {
			width: 16px;
			height: 4px;
			right: 48px;
			top: 44px;
			@include sm {
				top: 24px;
				right: 14px; } }
		&:after {
			width: 4px;
			height: 16px;
			right: 54px;
			top: 38px;
			@include sm {
				top: 18px;
				right: 20px; } }
		&.is-active {

			&:after {
				transform: scaleY(0); } } }
	&__body {
		display: none;
		padding: 30px 43px 13px;
		@include sm {
			padding: 17px 15px 0; } }
	&__list {
		line-height: 1.5;
		font-weight: 500; }
	&__item {
		font-size: 20px;
		font-weight: 700;
		position: relative;
		@include sm {
			font-size: 16px; }
		&:not(:first-child) {
			margin-top: 25px;
			@include sm {
				margin-top: 15px; } } } }
