.for-whom {
	position: relative;
	padding: 95px 0;
	@include clr;
	@include md {
		padding: 30px 0; }
	&__list {
		margin-top: 65px;
		@include sm {
			margin-top: 30px; } }
	&__item {
		font-size: 20px;
		font-weight: 700;
		padding-left: 70px;
		position: relative;
		@include sm {
			font-size: 16px;
			padding-left: 50px; }
		&:not(:first-child) {
			margin-top: 25px;
			@include sm {
				margin-top: 15px; } }
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background-color: $orange;
			@include circle(40);
			@include sm {
				@include circle(30); } } }
	&__img {
		position: absolute;
		right: 0;
		bottom: 40px;
		z-index: -1;
		@include md {
			position: static;
			margin-top: 20px;
			@include clr; }
		img {
			display: block;
			max-width: 100%;
			@include md {
				float: right; } } } }
