//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// Museo Sans Cyrillic
@font-face {
	@include fontdefault(normal, 100, 'MuseoSansCyrillic');
	@include fontpath('MuseoSansCyrillic ExtraLight', 'MuseoSansCyrillic-ExtraLight', MuseoSansCyrl-100); }
@font-face {
	@include fontdefault(normal, 300, 'MuseoSansCyrillic');
	@include fontpath('MuseoSansCyrillic Light', 'MuseoSansCyrillic-Light', MuseoSansCyrillic-300); }
@font-face {
	@include fontdefault(normal, 500, 'MuseoSansCyrillic');
	@include fontpath('MuseoSansCyrillic Semibold', 'MuseoSansCyrillic-Semibold', MuseoSansCyrillic-500); }
@font-face {
	@include fontdefault(normal, 700, 'MuseoSansCyrillic');
	@include fontpath('MuseoSansCyrillic Bold', 'MuseoSansCyrillic-Bold', MuseoSansCyrillic-700); }
@font-face {
	@include fontdefault(normal, 900, 'MuseoSansCyrillic');
	@include fontpath('MuseoSansCyrillic Black', 'MuseoSansCyrillic-Black', MuseoSansCyrillic-900); }
